.userWrapper {
    display: flex
    ;
        justify-content: center;
        gap: 20px;
        align-items: baseline;
  }



  .button {
    background-color: white;
    color: #1976d2;
    border: none;
    padding: 8px 16px;
    border-radius: 20px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .button:hover {
    background-color: #e3f2fd;
  }
 

  @media (max-width: 480px) {
   
    .button {
      padding: 6px 12px;
      font-size: 14px;
    }
  }
  
