.nav {
    display: flex;
    gap: 20px;
  }
  
  .link {
    color: white;
    text-decoration: none;
    font-weight: 500;
    transition: color 0.3s;
  }
  
  .link:hover,
  .active {
    text-decoration: underline;
  }


  @media (max-width: 768px) {
  
    
    .nav {
      flex-direction: column;
      gap: 12px;
      margin-bottom: 16px;
    }
  }
  
 
  @media (max-width: 480px) {
   
    .nav {
      gap: 8px;
    }
  
   
  }
  